import React from 'react';
import { Link } from 'react-router-dom';
import './Summary.css';

const Summary = (props) => (
    <div className="Sum">
        <h3>วัตถุดิบทั้งหมด</h3>
        <li>{props.ingredient}</li>
        <h3>ราคาต้นทุน</h3>
        <p>{props.totalPrice} บาท</p>
        <button className="Back"><Link to='/' >กลับ</Link></button> 
        <button className="Fin" onClick={props.reset} ><Link to='/' exact>เสร็จสิ้น</Link></button>
    </div>
);

export default Summary;