import React, { Component } from 'react';
import { Route, Link } from 'react-router-dom';
import Input from '../../components/Input/Input';
import Summary from '../../components/Summary/Summary';
import './CostCal.css';


class CostCal extends Component {

    state = {
        ingredients: [1],
        ingredientsVolume: [],
        ingredientsPrice: [],
        ingredientsUsed: [],
        ingredientsUnit: [],
        totalPrice: []
    }

    setIngredient = (ingredient, volume, price, unit, used) => {
      const totalPriceCalculated = (used*price/volume);
        this.setState({
            ingredients: this.state.ingredients.concat(ingredient),
            ingredientsVolume: this.state.ingredientsVolume.concat(volume),
            ingredientsPrice: this.state.ingredientsPrice.concat(price),
            ingredientsUnit: this.state.ingredientsUnit.concat(unit),
            ingredientsUsed: this.state.ingredientsUsed.concat(used),
            totalPrice: this.state.totalPrice.concat(totalPriceCalculated)
        });
    }

    resetIngredient = () => {
        this.setState({
            ingredients: [],
            ingredientsVolume: [],
            ingredientsPrice: [],
            ingredientsUnit: [],
            ingredientsUsed: [],
            totalPrice: []
        });
    }

  render() {
    
    const showIngredient = this.state.ingredients.map((ls, id) => {
        const valueOfUsed = ((this.state.ingredientsUsed[id]*this.state.ingredientsPrice[id])/this.state.ingredientsVolume[id]).toPrecision(3);
        return <div key={id} className="TextStyle">
                    <h4>Ingredient {id+1}  </h4> 
                        <p><b>วัตถุดิบ</b> : {ls} </p>
                        <p><b>ปริมาณ</b> : {this.state.ingredientsVolume[id]} {this.state.ingredientsUnit[id]} </p>
                        <p><b>ราคา</b> : {(this.state.ingredientsPrice[id]/this.state.ingredientsVolume[id]).toPrecision(3)} บาทต่อ {this.state.ingredientsUnit[id]} </p>
                        <p><b>ปริมาณที่ใช้</b> : {this.state.ingredientsUsed[id]} {this.state.ingredientsUnit[id]}</p>
                        <p><b>คิดเป็นเงิน</b> {valueOfUsed} บาท</p>
        </div>
    });

    const toSummary = (this.state.ingredients.length > 0 ?  <button className="Summa"><Link to='/summary'>สรุป</Link></button>: null )


    return(
      <div>
        <Route path="/" exact render={() => (
          <div> 
            <h1 style={{fontFamily: "'Pridi', serif"}}>สำหรับทดสอบ ยังไม่เสร็จสมบูรณ์ Version 0.0.3</h1>
              <Input
                  dataSubmit = {(ingredientSub, volumeSub, priceSub, unitSub, usedSub) => 
                                  this.setIngredient(ingredientSub, volumeSub, priceSub, unitSub, usedSub)}
              />

            <li>{showIngredient}</li> 
           {toSummary}
          </div>
          )} 
        />

     

     
        <Route path='/summary' render={() => {
          const totalPrice = this.state.totalPrice.reduce((total, ls) => {
            return total + ls;
          },0);
          return (
            <div>
                <Summary ingredient = {this.state.ingredients.map((ls, id) => ( <div key={id}> {ls} </div> ))}
                          totalPrice = {totalPrice}
                          reset={() => this.resetIngredient()}              
                />
                
            </div>
            )
          }} 
        />

      </div>
    );
  }
} 

export default CostCal;
