import React, { Component } from 'react';
import './Input.css'; 

class Input extends Component {

  state={
    ingredient: [],
    volume: [],
    price: [],
    unit: [],
    used: [],
    isActived: true
  }

  


    render() {
      const optionUnit = ["มิลลิกรัม","กรัม","กิโลกรัม","ฟอง","มิลลิลิตร","ลิตร"].map((list, index) => {
        return  <option value={list} key={index} >{list}</option>
      })
      let isActived = this.state.ingredient.length > 0? 
                        this.state.volume.length > 0? 
                          this.state.price.length >0? 
                            this.state.used.length>0? false: true 
                          : true 
                        : true 
                      : true;

        return(
          <div className="InputConfig">
            <input  type="text" 
                    placeholder="วัตถุดิบ" 
                    onChange={(event) => this.setState({ingredient: event.target.value})} value={this.state.ingredient} />
            
            <input  type="number" 
                    placeholder="ปริมาณ" 
                    onChange={(event) => this.setState({volume: event.target.value})} value={this.state.volume}/>

            <select onClick={(event) => this.setState({unit: event.target.value})} disabled={this.state.volume.length > 0? false: true}>
              <option defaultValue hidden>เลือกหน่วย</option>
              {optionUnit}
            </select>

            <input  type="number" 
                    placeholder="ราคา (บาท)" 
                    onChange={(event) => this.setState({price: event.target.value})} value={this.state.price}/> 

            <input  type="number" 
                    placeholder={"ปริมาณที่ใช้ไป (" + this.state.unit + ")"} 
                    onChange={(event) => this.setState({used: event.target.value})} value={this.state.used}/>

            <button type="submit" 
                    onClick={() => {this.props.dataSubmit(this.state.ingredient, this.state.volume, this.state.price, this.state.unit, this.state.used) ; 
                                    this.setState({ingredient: [], volume: [], price: [], unit: [], used: []}) } 
                            }
                    disabled={isActived}
                            >คำนวณ</button>
          
          </div>
        );
      }
} 

export default Input;